@font-face {
  font-family: "roboto-bold";
  src: url("../fonts/Roboto-Black.ttf");
}

@font-face {
  font-family: "roboto-medium";
  src: url("../fonts/Roboto-Medium.ttf");
}

@font-face {
  font-family: "roboto-regular";
  src: url("../fonts/Roboto-Regular.ttf");
}

@font-face {
  font-family: "roboto-thin";
  src: url("../fonts/Roboto-Thin.ttf");
}

@font-face {
  font-family: "roboto-light";
  src: url("../fonts/Roboto-Light.ttf");
}

@font-face {
  font-family: "YesevaOne-Regular";
  src: url("../fonts/YesevaOne-Regular.ttf");
}

.container {
  max-width: 1280px;
  padding: 0px 16px;
  margin: 0 auto;
}

.menu-bar {
  background-color: white;
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 5%;
  position: relative;
}

.menu-bar ul {
  list-style: none;
  display: flex;
}

.menu-bar ul li {
  /* width: 120px; */
  padding: 10px 30px;
  /* text-align: center; */
  position: relative;
}

.menu-bar ul li a {
  font-size: 20px;
  color: black;
  text-decoration: none;
  transition: all 0.3s;
}

.nav_icon {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.fas {
  float: right;
  margin-left: 10px;
  padding-top: 40px;
}

/* dropdown menu style */

.dropdown-menu {
  display: none;
  border-radius: 4%;
}

.menu-bar ul li:hover .dropdown-menu {
  display: block;
  position: absolute;
  left: 0;
  top: 100%;
  border: 1px solid #E9E9E9;
}

.menu-bar ul li:hover .dropdown-menu a {
  position: absolute;
}

.menu-bar ul li:hover .dropdown-menu ul {
  display: block;
  margin: 10px;
}

.menu-bar ul li:hover .dropdown-menu ul li {
  width: 150px;
  padding: 25px;
  border-bottom: 1px solid #E9E9E9;
}

.dropdown-menu-1 {
  display: none;
}

.dropdown-menu ul li:hover .dropdown-menu-1 {
  display: block;
  position: absolute;
  left: 150px;
  top: 0;
}

.nav__mobile {
  display: none;
}

