#burger-menu {
  cursor: pointer;
  height: 27px;
  width: 27px;
  margin: 10px;
  overflow: visible;
  position: relative;
  z-index: 2;
}

#burger-menu span,
#burger-menu span:before,
#burger-menu span:after {
  background: #0a0a0a;
  display: block;
  height: 4px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}

#burger-menu span:before,
#burger-menu span:after {
  content: "";
}

#burger-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}

#burger-menu span:before {
  left: 0px;
  top: -10px;
  width: 16px;
}

#burger-menu span:after {
  left: 0px;
  top: 10px;
  width: 20px;
}

#burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}

#burger-menu.close span:before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#burger-menu.close span:after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

#menu {
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  position: fixed;
  top: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
  text-align: center;
  padding-top: 20px;
  transition: all 0.3s ease-in-out;
}

#menu.overlay {
  visibility: visible;
  opacity: 1;
  padding-top: 100px;
  background: rgba(0, 0, 0, 0.5);
}

#menu li {
  list-style: none;
}

#menu a {
  color: #fff;
  display: block;
  font-size: 32px;
  margin-bottom: 30px;
  text-decoration: none;
}

.cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 20px;
  gap: 40px;
  border-color: #F2F2F2;
}

.card img {
  width: 360px;
  height: 230px;
  background-size: cover;
}

.card {
  position: relative;
  padding: 20px;
}

.details > h3 {
  color: #ffffff;
  font-weight: 600;
  font-size: 18px;
  margin: 10px 0 15px 0;
}

.details > h5 {
  color: #fa0707;
  font-weight: 600;
  font-size: 12px;
  margin: 10px 0 15px 0;
}

.details > p {
  color: #a0a0a0;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  position: absolute;
}

.xop-section {
  max-width: 960px;
  margin: 0 auto;
  padding: 6% 2%;
}

.xop-grid {
  margin: 20px 0 0 0;
  padding: 0;
  list-style: none;
  display: flex;
  text-align: center;
  width: 100%;
}

.xop-grid::after {
  clear: both;
}

.xop-grid::after,
.xop-grid::before {
  content: "";
  display: table;
}

.xop-grid li {
  width: 400px;
  height: 500px;
  display: inline-block;
  margin: 20px;
}

.xop-box {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  border-radius: 10px;
  -webkit-transition: 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out, -moz-transform 0.3s ease-in-out;
  transition: 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.xop-box:hover {
  transform: scale(1.05);
}

.xop-img-1 {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), url(../img/img1.png);
}

.xop-img-2 {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), url(../img/img2.png);
}

.xop-img-3 {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), url(../img/img4.png);
}

.xop-info {
  position: absolute;
  width: inherit;
  height: inherit;
}

.xop-info h3 {
  font-family: "YesevaOne-Regular" cursive;
  font-weight: 400;
  color: #fff;
  font-size: 42px;
  margin: 0 30px;
  padding: 100px 0 0 0;
  line-height: 1.5;
}

.xop-info p {
  font-family: "roboto-light" sans-serif;
  color: #fff;
}

body {
  font-family: roboto-regular, sans-serif;
}

@media screen and (max-width: 768px) {
  .nav__mobile {
    display: block;
  }

  .menu-bar ul {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    top: 0;
    left: -5555px;
    width: 250px;
    height: 100vh;
    background: #FFFFFF;
    gap: 15px;
    padding: 30px;
    transition: left 0.3s;
  }

  .menu-bar ul.active__nav {
    transition: left 0.3s;
    left: 0;
  }

  .menu-bar ul .menu-bar ul li a {
    color: #0b0b0b;
  }

  .menu-bar ul .nav_icon {
    position: relative;
    left: 20%;
  }
}